import Vue from 'vue'
import Vuex from 'vuex'

import {auth} from '../firebase'
import router from '../router'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    usuario: null,
    error: null,
    success: null,
    loading: false,
    snackbarFirebase: null,
    eventos: [],
    backup_eventos: [],
    funcionarios: [],
    backup_funcionarios: [],
    helper: false
  },
  mutations: {
    setUsuario(state, payload){
      state.usuario = payload
    },
    setError(state, payload){
      state.error = payload
    },
    clearError(state){
      state.error = null
    },
    setLoading(state, payload){
      state.loading = payload
    },
    setSnackbarFirebase(state, payload){
      state.snackbarFirebase = payload
    },
    setSuccess(state, payload){
      state.success = payload
    },
    setEventos(state, payload){
      state.eventos = payload
    },
    setBackupEventos(state, payload){
      state.backup_eventos = payload
    },
    setFuncionarios(state, payload){
      state.funcionarios = payload
    },
    setBackupFuncionarios(state, payload){
      state.backup_funcionarios = payload
    },
    setHelper(state, payload){
      state.helper = payload
    },
  },
  actions: {
    crearUsuario({commit}, usuario){
      commit('setLoading', true)
      commit('clearError')
      commit('setSuccess', null)
      auth.createUserWithEmailAndPassword(usuario.email, usuario.password)
        .then(res =>  {
          // console.log(res)
          const usuarioCreado = {
            email: res.user.email,
            uid: res.user.uid
          }
          // commit('setUsuario', usuarioCreado)
          commit('setLoading', false)
          commit('setSuccess', true)
          res.user.sendEmailVerification()
          //router.push('/')
        })
        .catch(error => {
          commit('setLoading', false)
          commit('setError', error)
          commit('setSnackbarFirebase', true)
          console.log(error)
        })
    },
    ingresoUsuario({commit}, usuario){
      //Activar spinner
      commit('setLoading', true)
      commit('clearError')
      auth.signInWithEmailAndPassword(usuario.email, usuario.password)
      .then(res => {
        console.log(res);
        const usuarioLogeado = {
          email: res.user.email,
          uid: res.user.uid
        }
        commit('setLoading', false)
        commit('setUsuario', usuarioLogeado)

        if (usuarioLogeado.email === "admin@gmail.com") {
          router.push('/admin')
        } else {
          router.push('/dashboard')
        }
      })
      .catch(error => {
        commit('setLoading', false)
        commit('setError', error)
        commit('setSnackbarFirebase', true)
        console.log(error)
      })
    },
    cerrarSesion({commit}){
      auth.signOut()
        .then(() => {
          router.push('/')
        })
    },
    detectarUsuario({commit}, usuario){
      commit('setUsuario', usuario)
    },
    restablecerContrase√±a({commit}, usuario){
      commit('setLoading', true)
      commit('clearError')
      auth.sendPasswordResetEmail(usuario)
      .then(res => {
        //Email se envia
        commit('setLoading', false)
        // commit('setSuccess', true)
        commit('setSnackbarFirebase', true)
      })
      .catch(error => {
        commit('setLoading', false)
        commit('setError', error)
        commit('setSnackbarFirebase', true)
        console.log(error)
      })
    },
    updateEventos({commit}, data){
      commit('setEventos', data)
    },
    updateBackupEventos({commit}, data){
      commit('setBackupEventos', data)
    },
    updateFuncionarios({commit}, array){
      commit('setFuncionarios', array)
    },
    updateBackupFuncionarios({commit}, array){
      commit('setBackupFuncionarios', array)
    }
  },
  modules: {
  }
})
