import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import vuetify from './plugins/vuetify';

import Vuelidate from 'vuelidate'
Vue.use(Vuelidate)

import axios from 'axios'
import VueAxios from 'vue-axios'
Vue.use(VueAxios, axios)

// axios.defaults.baseURL = 'http://localhost:3000'
axios.defaults.baseURL = 'http://52.186.13.53:3000'

Vue.config.productionTip = false

import {auth} from './firebase'

auth.onAuthStateChanged(user => {
  if(user){
    //console.log(user);
    const detectoUsuario = {
      email: user.email,   //No se coloca res.user.email porque ya estamos en un usuario
      uid: user.uid
    }
    store.dispatch('detectarUsuario', detectoUsuario)  //Como arriba importamos la store, podemos acceder directo a una accion
  } else {
    //console.log(user);
    store.dispatch('detectarUsuario', user)
  }

  new Vue({
    router,
    store,
    vuetify,
    render: h => h(App)
  }).$mount('#app')  

})